import React, { Component } from 'react';
import Farm from './Farm';
import Landing from './Landing';
import Docs from './Docs';
import { Switch, Route } from "react-router-dom";

class Content extends Component {

  render() {
    return (
      <div id="content">

        <Switch>

          <Route exact path="/">
            <Landing contracts={this.props.contracts} />
          </Route>

          <Route path="/farm">
            <Farm ethereumservice={this.props.ethereumservice} contracts={this.props.contracts} />
          </Route>

          <Route path="/docs">
            <Docs />
          </Route>

        </Switch>
      </div>
    );
  }
}

export default Content;
