import React, { Component } from 'react';
import logo from '../assets/logo.png';
import { Link } from 'react-router-dom';

class NavBar extends Component {
  constructor(props) {
    super(props);

    this.state = {
      menuActiveClass: ''
    };

    this.handleBurgerClick = this.handleBurgerClick.bind(this);
    this.menuItemClick = this.menuItemClick.bind(this);
  }

  handleBurgerClick(e) {
    this.setState({
      menuActiveClass: this.state.menuActiveClass === '' ? 'is-active' : ''
    });
  }

  menuItemClick(e) {
    this.setState({
      menuActiveClass: ''
    });
  }

  render() {
    return (
      <nav className="navbar is-fixed-top is-transparent" role="navigation" aria-label="main navigation">
        <div className="navbar-brand">
          <Link className="navbar-item" exact="true" to="/">
            <img className="logo" src={logo} width="35" />
            <span className="navbar-item logo-text">FARMLAND</span>
          </Link>
          <a
            onClick={this.handleBurgerClick}
            role="button"
            className={`navbar-burger burger ${this.state.menuActiveClass}`}
            aria-label="menu"
            aria-expanded="false"
            data-target="navbar-menu-items">
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
          </a>
        </div>

        <div
          id="navbar-menu-items"
          className={`navbar-menu ${this.state.menuActiveClass}`}
          onClick={this.menuItemClick}>
          <div className="navbar-start">
            <Link className="navbar-item" to="/farm">
              Farm
            </Link>
            <Link className="navbar-item" to="/docs">
              Docs
            </Link>
          </div>

          <div className="navbar-end">
            <div className="navbar-item">
              <div className="buttons is-centered">
                <a className="button is-info" target="_blank" rel="noopener noreferrer" href="https://app.uniswap.org/#/swap?inputCurrency=eth&amp;outputCurrency=0x112b2060E01000194d13aF3B15874fF5599cD0f6"> {this.props.contracts.landBalance} LAND </a>
                <a className="button is-danger">{this.props.contracts.ethBalance} ETH</a>
                <a className="button is-dark">{this.props.contracts.walletAddress.substring(0,6)}...{this.props.contracts.walletAddress.substring(this.props.contracts.walletAddress.length-4)}&nbsp;<ion-icon name="link-outline"></ion-icon>
                </a>
              </div>
            </div>
          </div>
        </div>
      </nav>
    );
  }
}

export default NavBar;
